import Vue from 'vue'
import Router from 'vue-router'
import Top from '@/pages/top'
import About from '@/pages/about'

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: 'test/vue-router-test',
  routes: [
    {
      path: '/',
      name: 'Top',
      component: Top
    },
    {
      path: 'about',
      name: 'about',
      component: About
    }
  ]
})



